.header {
  display: flex;
  width: 100%;
  height: 76px;
  border-bottom: 2px solid rgba(17, 17, 17, 0.1);
  background: #FFFFFF;
}

.header__logo_container img {
  position: absolute;
  top: 24px;
  left: 92px;
}

.header__links_container {
  width: 134px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.header__links_container a {
  color: rgba(17, 17, 17, 0.48);
  text-decoration: none;
}